<style>
  .sidelist {
    width: 20em;
  }
</style>

<script>
  import { writable } from "svelte/store";
  import { onMount } from "svelte";
  import { createAuth, getAuth } from "../auth.js";

  import axios from "axios";
  import { getRoom, getRooms, updateRoom } from "../api/rooms.js";

  let rooms = [];
  let selectedRoom = {};

  const {
    isLoading,
    isAuthenticated,
    login,
    logout,
    authToken,
    authError,
    userInfo,
  } = getAuth();

  $: state = {
    isLoading: $isLoading,
    isAuthenticated: $isAuthenticated,
    authError: $authError,
    userInfo: $userInfo ? $userInfo.name : null,
    authToken: $authToken.slice(0, 20),
    rooms,
  };

  $: {
    getRooms($authToken, (data) => {
      rooms = data;
      selectedRoom = rooms [0]
    });
  }

  onMount(async () => {
    var elems = document.querySelectorAll(".tabs");
    let instance = M.Tabs.init(elems);

    document.body.style.backgroundImage = "";
  });
</script>

<div class="row">
  <h4>Manage Rooms</h4>

  <div class="col s3">
    <div class="collection">
      {#each rooms as room}
        <a href="#!" class="collection-item">{room.name}</a>
      {/each}
    </div>
  </div>

  <form>

    <div class="input-field col s6">
      <input placeholder="Room ID" id="room_id" type="text" value="{selectedRoom.id}" disabled />
      <label for="room_id">Room ID</label>

      <input placeholder="Room Name" id="room_name" type="text" value="{selectedRoom.name}"/>
      <label for="room_name">Room Name</label>
    </div>

  </form>

</div>

